import type { ServerResponse } from 'http';

import { IS_MULTI_TENANT } from '@/env';
import { getS3URI } from '@/utils/s3';

function LlmsFullTxt() {}

function getLlmsFullURI(subdomain: string) {
  return getS3URI(subdomain, '_generated/llms-full.txt');
}

export async function getServerSideProps({
  params: { subdomain },
  res,
}: {
  params: { subdomain: string };
  res: ServerResponse;
}) {
  if (!IS_MULTI_TENANT) {
    return {
      notFound: true,
    };
  }

  try {
    const response = await fetch(getLlmsFullURI(subdomain));

    if (!response.ok) {
      throw new Error('Failed to fetch llms.txt');
    }
    const llmTxt = await response.text();

    res.setHeader('Content-Type', 'text/plain; charset=utf-8');
    res.setHeader('Cache-Control', 'public, max-age=86400');
    res.write(llmTxt);
    res.end();

    return {
      props: {},
    };
  } catch (error) {
    console.error('Error fetching llms-full.txt:', error);
    return {
      notFound: true,
    };
  }
}

export default LlmsFullTxt;
